import React, {Component} from 'react'
import PropTypes from 'prop-types'

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class HiddenDivDownloaderButton extends Component {
    render() {
      const {id, label, filename, hiddenDivData} = this.props

      const downloadChildCSV = () => {
        const csvStr = JSON.parse(hiddenDivData)[filename]
        const blob = new Blob([csvStr], {type: 'text/csv'})
        if(window.navigator.msSaveOrOpenBlob) {
          window.navigator.msSaveBlob(blob, filename)
        }
        else {
          const elem = window.document.createElement('a')
          elem.href = window.URL.createObjectURL(blob)
          elem.download = filename + '.csv'
          document.body.appendChild(elem)
          elem.click()
          document.body.removeChild(elem)
        }
      }
      return (
        <div id={id}>
          <button onClick={downloadChildCSV}>
            {label}
          </button>
          <div style={{ display: 'none' }}>
            {hiddenDivData}
          </div>
        </div>
      )
    }
}

HiddenDivDownloaderButton.defaultProps = {}

HiddenDivDownloaderButton.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

  /**
   * A label that will be printed when this component is rendered.
   */
  label: PropTypes.string.isRequired,

  /**
   * The basename of the csvStr file to download.
   */
  filename: PropTypes.string,

  /**
   * String that will be provided as a download.
   */
  hiddenDivData: PropTypes.string,
}
